<template>
  <div class="hourly-weather">
    <div class="container">
      <div
        class="hourly-temp"
        v-for="(time, index) in filteredList"
        :key="index">
        <HourlyTemp
          :time="time"
        />
      </div>
    </div>
  </div>
</template>

<script>
import HourlyTemp from "./HourlyTemp";
export default {
  name: "HourlyWeather",
  props: ["forecast"],
  components: {
    HourlyTemp,
  },
  created() {
    console.log(this.forecast);
  },
  computed: {
      filteredList() {
          return this.forecast.hourly.slice(0, 23);
      },
  },
};
</script>

<style lang="scss" scoped>

.hourly-weather {
    padding: 10px 0;
    border-bottom: 1px solid rgba($color: white, $alpha: 0.7);
    .container {
        display: flex;
        max-width: 100vw;
        overflow: scroll;
        padding: 10px 0;
        margin: 0 20px;
        &::-webkit-scrollbar {
            display: none;
        }
    }
}

</style>